.times {
    float: right;
    top: 0;
}
.modal {
    position: fixed;
    top: 0;
    left: 0;
    width:100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.75);
} 
.modal-main {
    text-align: center;
    z-index:25;
    position:fixed;
    background: rgb(17, 17, 17);
    width: 80%;
    height: auto;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
    border-radius: 15px;
    padding:2%;
    box-sizing: border-box;
} 
.display-block {
    display: block;
} 
.display-none {
    display: none;
}
