.submit-btn{
    text-align: center;
    text-decoration: none;
    margin: 0 3vw;
    background:transparent;
    outline:none;
    border:1px solid dodgerblue;
    /* height:30px; */
    color:dodgerblue;
    height: auto;
    width: auto;
    font-size:20px;
    border-radius: 5px;
    cursor: pointer;
}
.submit-btn:hover{
    background: dodgerblue;
    color:white;
}
.submit-btn:active{
    transform: scale(0.9);
}
